$main-color: #69ca62;
$remgika: #c7cda4;

// /*********************************************************
//   **********************************************************
//   ************************ MainEventsground ***********************
//   **********************************************************
//   **********************************************************/

.MainEventsground {
  .serchBar {
    width: 100%;
    .MainEvents-autocomplte {
      margin-top: 20px;
      display: flex;
      justify-content: center;
      width: 100%;
      .inpute-auto {
        width: 70%;
        display: flex;
        flex-direction: column;
        input,
        input:focus {
          background-image: url("../img/search-64.png");
          background-repeat: no-repeat;
          background-position: -2px -5px;
          padding: 10px 20px 12px 50px;
          background-size: 50px;
          height: 45px;
          border: solid 3px rgba(106, 106, 240, 0.418);
          border-radius: 10px;
          box-shadow: 4px 4px 4px #00000025;
          margin-top: 13px;
          background-color: rgba(106, 106, 240, 0.061);
          color: black;
          font-size: 15px;
          letter-spacing: 1px;
          outline: none;
        }
        input:hover {
          background-color: white;
        }

        ul {
          padding: 0;
          border: solid 3px #c7cda4;
          border-bottom-left-radius: 8px;
          border-bottom-right-radius: 8px;
          border-width: 1px 1px 1px;
          border-style: none solid solid;
          list-style-type: none;
          text-align: left;
          border-image: initial;
          border-top: none;
          li {
            margin: 5px;
          }
        }
      }
      .camera-flex {
        width: 25%;
        display: flex;
        flex-direction: column;
        img {
          // width: 100%;
          height: 70px;
          color: $remgika;
          cursor: pointer;
        }
        span {
          font-size: 13px;
          letter-spacing: 1px;
        }
      }
    }
  }
  /**********************************************************
******************** MainEventsGROUND ITEMS *********************
***********************************************************/
  .categorie {
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
      text-align: left;
      align-self: flex-start;
      margin-left: 5%;
    }
    hr {
      height: 5px;
      width: 90%;
      background-color: rgba(58, 57, 57, 0.335);
    }
  }
  .carouselDiv {
    display: flex;
    flex-direction: column;
    padding: 40px;
    margin: 0;
    ::-webkit-scrollbar {
      display: none;
    }
    .events-item {
      border-radius: 10px;
      box-shadow: -1px -1px 4px #dcdef19f, rgb(226, 216, 216) 1px 1px 4px;
      overflow: hidden;
      padding: 20px;
      transition: transform ease-in-out 0.6s;
      height: 950px;
      .userVote {
        img {
          width: 20px;
          height: 20px;
          cursor: pointer;
        }
        :first-child {
          margin-right: 10px;
        }
      }
      .image {
        position: relative;
        img {
          border-radius: 20px;
          width: 99%;
          object-fit: cover;
          height: 400px;
          transition: transform ease-in-out 0.3s;
        }
        // img:hover { 
        //   transform: scale(1.2);
        //   scroll-snap-type: none;
        //   scroll-behavior: inherit;
        // }
        .info {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          justify-content: space-between;
          align-content: flex-start;
          position: absolute;
          top: 0;
          width: 100%;
          .address {
            background: #9167c9;
            padding: 2px 5px;
            border-radius: 5px;
            color: white;
          }
          .address-info {
            background: #9167c9;
            padding: 2px 5px;
            border-radius: 5px;
            color: white;
          }
        }
        .dictanceInfo {
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          position: absolute;
          bottom: 6px;
          width: 100%;
          .address-info {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-content: flex-start;
            position: absolute;
            bottom: 0;
            width: 100%;
            .addressDistance {
              display: flex;
              background: #9167c9;
              justify-content: space-between;
              border-radius: 5px;
              padding: 2px 5px;
              color: white;
            }
            .addressDuration {
              display: flex;
              background: #9167c9;
              justify-content: space-around;
              border-radius: 5px;
              padding: 2px 5px;
              color: white;
            }
          }
        }
      }
      h3 {
        font-size: 18px;
        font-weight: 400;
        height: 60px;
      }

      .timeItem {
        display: flex;
        flex-direction: column;
        text-align: center;
        padding: 5px;
        border-radius: 5px;
        justify-content: space-around;
        align-items: center;
        align-content: center;
      box-shadow: -1px -1px 4px #dcdef19f, rgb(226, 216, 216) 1px 1px 4px;

        span {
          color: rgb(30, 112, 5);
          margin: 1px;
        }
      }
      .author {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 1px;
        p {
          margin: 5px;
          font-weight: 400;
        }
      }
      .description {
        border: solid 1px rgba(106, 106, 240, 0.418);
        border-radius: 2px;
        text-align: center;
        padding: 4px;
        min-height: 200px;
        p {
          box-sizing: border-box;
          word-wrap: break-word;
        }
      }
      .mapLogo {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        span {
          color: rgb(189, 65, 65);
        }
        img {
          width: 20%;
          height: 50px;
        }
      }
      .map.hide {
        visibility: hidden;
        display: none;
      }
      .map.show {
        visibility: visible !important;
        background-color: rgb(216, 212, 212);
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0px;
        z-index: 1;
        animation-name: mapTransitionEvents;
        animation-duration: 1s;
        transition: width 2s linear 1s;
        .close-container {
          display: flex;
          justify-content: flex-end;
          height: 50px;
          img {
            width: 50px;
            height: 50px;
          }
        }
        .map {
          height: 80vh !important;
          z-index: 2;
        }
      }
    }
      /**********************************************************
******************** MainEventsGROUND ITEMS hover *********************
***********************************************************/

    
  @keyframes mapTransitionEvents {
      0% {
        top: 500px;
      }
      100% {
        top: 0px;
      }
    }
    .kaHnvc {
      height: 70%;
      top: 30px;
      width: 30px;
      button,
      button:focus {
        color: $remgika;
        width: 90%;
        height: 100%;
        font-size: 50px;
        clip-path: polygon(0 47%, 100% 100%, 100% 0);
        background-color: rgba(106, 106, 240, 0.418);
        border: 0;
        outline: none;
      }
    }
    .dFFQiB {
      height: 70%;
      top: 30px;
      width: 30px;
      button,
      button:focus {
        color: $remgika;
        width: 90%;
        height: 100%;
        font-size: 50px;
        clip-path: polygon(100% 49%, 0 0, 0 100%);
        background-color: rgba(106, 106, 240, 0.418);
        border: 0;
      }
    }
  }
}
@media (min-width: 500px) {
  .MainEventsground{
      .events-item{
        .ReactCollapse--collapse{
          position: relative;
          top: -790px;
          .map.show {
            background-color: antiquewhite;
            height: 880px;
          }
        }
        .feedback.show{
          background-color: antiquewhite;
          height: 880px;
        }
      }
      .events-item:hover{
        transform: scale(1.05);
      }
  }
}
/**********************************************************
******************** MainEventsGROUND Bid Devices*********************
